import React from 'react';
import {  Navigator, Ripple } from 'react-onsenui';
import styles from './assets/Entry.module.scss';

type Props = {
	bg: string;
	nextPage: any;
	nextPageKey: string;
	navigator: Navigator;
};
const Entry = (props: Props): JSX.Element => {

    const gotoPage = ()=>{
        props.navigator.pushPage({
			component: props.nextPage,
			props: {
				key: props.nextPageKey,
				navigator: props.navigator,
			},
		});
    }

	return (
		<div
            className={styles.pod}
            onClick={gotoPage}
		>
            <div className={styles.dice}>
                <img src={props.bg} alt=""/>
            </div>
            
        </div>
	);
};

export default Entry;
