import React, { Fragment } from 'react';

import { Button, Navigator, Ripple } from 'react-onsenui';
import styles from './assets/ListSection.module.scss';
import { makePy1 } from '../../utils/Pinyin';
import { sort as arraySort } from '../../utils/Array';

type IProps = {
	navigator: Navigator;
	nextPage: any;
	nextPageKey: string;
	listStyle: boolean;
	listData: Array<any>;
};

const sortDataByPy = (arr: Array<any>, byProp: string): { [key: string]: any[] } => {
	const alpha = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
	let hashList: { [key: string]: any[] } = {};
	let sortedArr: Array<any> = arraySort(arr, byProp, function (a, b) {
		let A = makePy1(a[byProp]).toUpperCase(),
			B = makePy1(b[byProp]).toUpperCase();
		return A === B ? 0 : A > B ? 1 : -1;
	});

	for (let i = 0, l = sortedArr.length; i < l; i++) {
		let it = sortedArr[i],
			f = makePy1(it[byProp]);

		if (!hashList[f]) {
			if (alpha.indexOf(f) === -1) f = '#';
			hashList[f] = [];
		}
		it[':index'] = i;
		hashList[f].push(it);
	}

	return hashList;
};

const ListSection = (props: IProps): JSX.Element => {
	const { navigator, listStyle, listData } = props;

	const pushNextPage = (item: any) => {
		props.navigator.pushPage({
			component: props.nextPage,
			props: {
				key: props.nextPageKey,
				navigator,
				item,
			},
		});
	};

	const renderList = (): JSX.Element[] => {
		let jsxElems: JSX.Element[] = [];
		let sortedHash = sortDataByPy(listData, 'name');
		for (let key in sortedHash) {
			let data = sortedHash[key];
			let header = <h3>{key}</h3>;
			let ul = (
				<ul className={styles.resultUL}>
					{data.map((item) => (
						<li
							key={item.id}
							onClick={() => {
								pushNextPage(item);
							}}
						>
							<Ripple color='rgba(0, 0, 0, 0.1)' background='rgba(0, 0, 0, 0.2)' />
							<div>
								<img
									src={item.logo}
									className={styles.compLogo}
								/>
								{item.name}
							</div>
						</li>
					))}
				</ul>
			);

			jsxElems.push(header);
			jsxElems.push(ul);
		}
		return jsxElems;
	};

	return (
	<div className={listStyle ? styles.resultSectionList : styles.resultSectionThumb}>{renderList()}</div>);
};

export default ListSection;
