async function post<T>(tUrl: string, params?: any) {
	const CancelToken = axios.CancelToken;
	const source = CancelToken.source();
	//axios.defaults.headers.common['x-access-token'] = JsonStorage.getSession<string>('token');

	try {
		let resp = await axios.post<ApiResult<T>>(tUrl, params, { cancelToken: source.token });
		return parseResponse<T>(resp);
	} catch (e) {
		console.error(e.message);
		throw e;
	}
}

async function get<T>(tUrl: string, source: CancelTokenSource, params?: any) {
	try {
		let resp = await axios.get<ApiResult<T>>(tUrl, { params, cancelToken: source.token });
		return parseResponse<T>(resp);
	} catch (e) {
		console.error(e.message);
		throw e;
	}
}

function parseResponse<T>(response: AxiosResponse<ApiResult<T>>): T {
	let resp: ApiResult<T> = {
		result: response.data.result,
		message: response.data.message,
		code: response.data.code,
		headers: response.headers,
	};

	if (resp.code === '000000') {
		return resp.result as T;
	} else {
		throw new Error(resp.message);
	}
}

function delay(t: number, v?: any): Promise<any> {
	return new Promise(function (resolve) {
		setTimeout(resolve.bind(null, v), t);
	});
}

export { get, post , delay};
