import React, { Fragment, useState, useEffect } from 'react';
import { Navigator, Page, Toolbar, BackButton } from 'react-onsenui';
import { withTranslation, WithTranslation, useTranslation } from 'react-i18next';
import { useObserver } from 'mobx-react';

import { exhibitorStore } from '../../stores';
import LoadingModal from '../../components/LoadingModal';
import { SearchBar, ListSection } from '../../components/ThumbList';
import ExhibitorDetailPage from './Detail';
type Props = {
	navigator: Navigator;
};

const Exhibitors = (props: Props & WithTranslation): JSX.Element => {
	const { t } = useTranslation();

	useEffect(() => {
		(async () => {
			await exhibitorStore.fetchData('');
		})();
	}, []);

	const handleSearch = (keyword: string) => {
		console.log(keyword);
		exhibitorStore.fetchData(keyword);
	};

	const handleToggleStyle = (isListStyle: boolean) => {
		exhibitorStore.toggleViewStyle(isListStyle);
	};

	const renderToolbar = () => (
		<Toolbar>
			<div className='left'>
				<BackButton></BackButton>
			</div>
			<div className='center'>{t('Exhibitors.exhibitorList')}</div>
		</Toolbar>
	);

	return useObserver(() => (
		<Page renderToolbar={renderToolbar}>
			<SearchBar onSearch={handleSearch} onToggleStyle={handleToggleStyle} />
			{exhibitorStore.isLoading ? (
				<LoadingModal />
			) : (
				<ListSection
					listData={exhibitorStore.exhibitors}
					listStyle={exhibitorStore.isListView}
					navigator={props.navigator}
					nextPage={ExhibitorDetailPage}
					nextPageKey={'exhibitorDetail'}
				/>
			)}
		</Page>
	));
};

export default withTranslation()(Exhibitors);
