import React from 'react';
import { observer, inject } from 'mobx-react';
import {
	Page,
	Toolbar,
	ToolbarButton,
	Icon,
	Splitter,
	SplitterSide,
	SplitterContent,
	Modal,
	Navigator,
} from 'react-onsenui';

import SideBar from './SideBar';

import Dashboard from './Dashboard';

import {commonStore, dashboardStore} from '../../stores';

interface Props {
	navigator: Navigator;
}

@observer
class Oracle extends React.Component<Props, {}> {
	private openDrawer = (): void => {
		dashboardStore.setDrawerOpen(true);
	};
	private closeDrawer = (): void => {
		dashboardStore.setDrawerOpen(false);
	};

	render() {
		return (
			<Page>
				<Splitter>
					<SplitterSide
						side='left'
						width={250}
						collapse={true}
						swipeable={true}
						isOpen={dashboardStore.drawerOpen}
						onClose={this.closeDrawer}
						onOpen={this.openDrawer}
					>
						<SideBar />
					</SplitterSide>
					<SplitterContent>
						<Dashboard navigator={this.props.navigator}></Dashboard>
					</SplitterContent>
				</Splitter>
			</Page>
		);
	}
}

export default Oracle;
