import React from 'react';
import ons from 'onsenui';
import { Navigator, Page, Button, ToolbarButton, Toolbar, Icon } from 'react-onsenui';
import { commonStore, dashboardStore } from '../../stores';
import { useTranslation, withTranslation, WithTranslation } from 'react-i18next';
import styles from './Dashboard.module.scss';
import Entry from './Entry';

import Exhibitors from '../Exhibitors';
import ExhibitorBg from './assets/exhibitor.png';
import BuyerBg from './assets/buyer.png';

type Props = {
	navigator: Navigator;
};

const Dashboard = (props: Props): JSX.Element => {
	//const { t, i18n } = useTranslation();

	const toggleDrawer = (): void => {
		dashboardStore.setDrawerOpen(!dashboardStore.drawerOpen);
	};

	const renderToolbar = (): JSX.Element => (
		<Toolbar>
			<div className='left'>
				<ToolbarButton onClick={toggleDrawer}>
					<Icon icon='md-menu' />
				</ToolbarButton>
			</div>
			<div className='center'>{commonStore.exhibitionName}</div>
		</Toolbar>
	);

	return (
		<Page renderToolbar={renderToolbar}>
			<div className='p-20'>
				<img src={'https://monaca.io/img/logos/download_image_onsenui_01.png'} className={styles.bannerImage} />
				<div className={styles.entryContainer}>
					<Entry
						navigator={props.navigator}
						bg={ExhibitorBg}
						nextPage={Exhibitors}
						nextPageKey={'ExhibitorList'}
					></Entry>
					<Entry
						navigator={props.navigator}
						bg={BuyerBg}
						nextPage={Exhibitors}
						nextPageKey={'BuyerList'}
					></Entry>
				</div>
			</div>
		</Page>
	);
};

export default Dashboard;
