import React from 'react';
import { withTranslation, WithTranslation } from 'react-i18next';
import ons from 'onsenui';
import { Button, Navigator, Page, Toolbar, BackButton } from 'react-onsenui';
import * as IECO from '@/interfaces';
import styles from './index.module.scss';

interface IProps extends IECO.BasicInformations {
}



class Regist extends React.Component<IProps & WithTranslation, {}> {

	render() {
		const { t } = this.props;

		return (
			<Page>
				<div className={styles.regPanel}>
					<Button>{t('Register.regist')}</Button>
				</div>
			</Page>
		);
	}
}

export default withTranslation()(Regist);
