import React, { useState } from 'react';
import { Input } from 'react-onsenui';
import styles from './ClearableInput.module.scss';

type Props = {
	onValueChange: Function;
	type?: string;
	placeholder?: string;
	style?: any;
	className?: any;
	modifier?: string;
	disabled?: boolean;
	readOnly?: boolean;
	onBlur?: (e: React.FocusEvent<any>) => void;
	onFocus?: (e: React.FocusEvent<any>) => void;
	value?: string;
	defaultValue?: string;
};

export default function ClearableInput(props: Props): JSX.Element {
	const [text, setText] = useState('');

	//const { onValueChange } = props;

	const clear = () => {
		setText('');
		props.onValueChange('');
	};

	return (
		<div className={styles.ClearableInput}>
			<Input
				className={styles.clearbleIpt}
				value={text}
				onChange={(event: React.ChangeEvent<any>) => {
					setText(event.target.value);
					props.onValueChange(event.target.value);
				}}
				{...props}
			/>
			{text && (
				<div role='img' className={styles.overlayClear} onClick={clear}>
					&times;
				</div>
			)}
		</div>
	);
}
