import { observable, action, computed, configure, runInAction } from 'mobx';

export class DashboardStore {
	@observable
	public drawerOpen: boolean;

	constructor() {
		this.drawerOpen = false;
	}

	@action
	setDrawerOpen(t: boolean) {
		this.drawerOpen = t;
	}
}

export default new DashboardStore();
