import { observable, action, computed, configure, runInAction } from 'mobx';
import { get, post, delay } from '../utils/Request';
import JsonStorage from '../utils/JsonStorage';
import i18n from 'i18next';
import * as EcoTypes from '../interfaces';

configure({ enforceActions: 'observed' });

const AUTH_TOKEN: string = EcoTypes.AUTH_TOKEN;
const USER_PROFILE: string = EcoTypes.USER_PROFILE;

export class CommonStore {
	@observable
	public token: string;

	public apiServer: string;

	@observable
	public orgId: string;

	@observable
	public orgLogo: string;

	@observable
	public orgName: string;

	@observable
	public exhibitionName: string;

	constructor() {
		this.apiServer = process.env.API_BASE_URL;
		this.token = JsonStorage.getSession(AUTH_TOKEN);

		this.orgId = '';
		this.orgLogo = '';
		this.orgName = '';
	}

	@action
	setOrgInfo() {
		this.orgId = 'xxx';
		this.orgLogo =
			'https://gblobscdn.gitbook.com/spaces%2F-L9iS6WpW81N7RGRTQ-K%2Favatar.png?generation=1523345851027218&alt=media';
		this.orgName = 'ITB';
		this.exhibitionName = 'ITV Next ExhibitionName';
	}

	@action
	async fetchBaseInfo() {
		try {
			await delay(1e3);
			this.setOrgInfo();
		} catch (e) {}
	}

	@action
	async login(username: string, password: string) {
		await delay(2e3);
		this.setToken('authorization');
		JsonStorage.setSession(USER_PROFILE, {
			username,
		});
	}

	@action
	setToken(token: string) {
		JsonStorage.setSession(AUTH_TOKEN, token);
		this.token = token;
	}

	@action
	removeToken() {
		JsonStorage.removeSession(AUTH_TOKEN);
		this.token = undefined;
	}

	@computed
	get isLogin(): boolean {
		return !!this.token;
	}
}

export default new CommonStore();
