import React from 'react';
import { observer, inject } from 'mobx-react';
import commonStore, { CommonStore } from '../../stores/CommonStore';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import 'onsenui/css/onsenui.min.css';
import 'onsenui/css/onsen-css-components.min.css';
import LoadingModal from '../../components/LoadingModal';
import MainBoard from '../MainBoard';
import Login from '../Login';
import Regist from '../Regist';

@inject('commonStore')
@observer
class App extends React.Component {

	/**
	 * fetch base informations by domain name
	 */
	public async componentDidMount() {
		await commonStore.fetchBaseInfo();
	}

	render() {
		const { token, orgLogo, orgName, orgId, exhibitionName } = commonStore;

		if (!orgId) {
			return (
				<LoadingModal />
			);
		} else if (token) {
			return <MainBoard commonStore={commonStore} />;
		} else {
			return (
				<Router>
					<Switch>
						<Route exact path='/'>
							<Login
								exhibitionName={exhibitionName}
								orgId={orgId}
								orgName={orgName}
								orgLogo={orgLogo}
							/>
						</Route>
						<Route path='/regist'>
							<Regist
								exhibitionName={exhibitionName}
								orgId={orgId}
								orgName={orgName}
								orgLogo={orgLogo}
							/>
						</Route>
					</Switch>
				</Router>
			);
		}
	}
}

export default App;
