import React, { useState } from 'react';
import styles from './assets/SearchBar.module.scss';
import exhibitorStore from '../../stores/ExhibitorStore';

import { useTranslation } from 'react-i18next';

type Props = {
	onSearch: (keyword: string) => void;
	onToggleStyle: (isListStyle: boolean) => void;
};
declare global {
	namespace JSX {
		interface IntrinsicElements {
			'ons-search-input': React.DetailedHTMLProps<React.HTMLAttributes<HTMLInputElement>, HTMLInputElement>;
		}
	}
}

const SearchBar = (props: Props): JSX.Element => {
	const [text, setText] = useState('');
	const { t } = useTranslation();

	const { onSearch, onToggleStyle } = props;

	const handleInput = (event: any): void => {
		setText(event.target.value);
	};

	const doSearch = (e: any) => {
		onSearch(text);
	};

	return (
		<section className={styles.barContainer}>
			<div
				className={exhibitorStore.isListView ? styles.taggleListOn : styles.taggleListOff}
				onClick={() => {
					onToggleStyle(true);
				}}
			></div>
			<div
				className={exhibitorStore.isListView ? styles.taggleThumbOff : styles.taggleThumbOn}
				onClick={() => {
					onToggleStyle(false);
				}}
			></div>

			<ons-search-input style={{ float: 'right' }}>
				<input
					type='search'
					className='search-input'
					placeholder={t('Common.Search')}
					value={text}
					onChange={handleInput}
					// @ts-ignore
					ref={(element) => ((element || {}).onsearch = doSearch)}
				/>
			</ons-search-input>
		</section>
	);
};

export default SearchBar;
