import React from 'react';
import { Button, Navigator, Page, Toolbar, BackButton } from 'react-onsenui';
import { withTranslation, WithTranslation, useTranslation } from 'react-i18next';
import styles from './assets/Detail.module.scss';

type IProps = {
	navigator: Navigator;
	item: any;
};

const ExhibitorDetail = (props: IProps & WithTranslation) => {
	const { t } = useTranslation();

	const { item } = props;

	const renderToolbar = () => (
		<Toolbar>
			<div className='left'>
				<BackButton></BackButton>
			</div>
			<div className='center'>{t('Exhibitors.exhibitorDetail')}</div>
		</Toolbar>
	);

	return (
		<Page renderToolbar={renderToolbar}>
			<div className={styles.infoContainer}>
				<div className={styles.headSec}>
					<img src={item.logo} className={styles.logo} />
					{item.name || ''}
				</div>
				<div className={styles.infoSec}>
				其它几个属性，我都搞懂了引用static : 无特殊定位，对象 ...
				</div>
			</div>
		</Page>
	);
};

export default withTranslation()(ExhibitorDetail);
