import React from 'react';
import ons from 'onsenui';
import commonStore, { CommonStore } from '../../stores/CommonStore';
import { Button, Navigator, Page } from 'react-onsenui';
import styles from './assets/index.module.scss';
import bg from './assets/bg.png';

import { withTranslation, WithTranslation } from 'react-i18next';

import * as IECO from '@/interfaces';
import ClearableInput from '../../components/ClearableInput';

interface Props extends IECO.BasicInformations {
	navigator?: any;
}

type State = {
	username: string;
	password: string;
};

class Login extends React.Component<Props & WithTranslation, State> {
	public state = {
		username: '',
		password: '',
	};

	// pushPage = () => {
	// 	this.props.navigator.pushPage({
	// 		component: Regist,
	// 		props: { key: 'registPage' },
	// 	});
	// };

	handleUsernameValue = (val: string) => {
		this.setState({ username: val });
	};

	handlePasswordValue = (val: string) => {
		this.setState({ password: val });
	};

	doLogin = () => {
		if (this.state.username) {
			commonStore.login(this.state.username, this.state.password);
		} else {
			ons.notification.alert('username');
		}
	};

	render() {
		const { t } = this.props;
		return (
			<Page className={styles.whiteBg}>
				<img src={bg} className={styles.LoginBg} />

				<section className={styles.LoginPanel}>
					<img src={this.props.orgLogo} alt='' className={styles.LoginLogo} />
					<p className={styles.exiTitle}>{this.props.exhibitionName}</p>
					<p>
						<ClearableInput
							type='text'
							modifier='material'
							placeholder={t('Login.email / mobile')}
							style={{
								width: '100%',
							}}
							onValueChange={this.handleUsernameValue}
						></ClearableInput>
					</p>
					<p>
						<ClearableInput
							type='password'
							modifier='material'
							placeholder={t('Login.password')}
							style={{
								width: '100%',
							}}
							onValueChange={this.handlePasswordValue}
						></ClearableInput>
					</p>
					<p>
						<Button
							modifier='large'
							onClick={this.doLogin}
							disabled={
								!(this.state.username &&
								this.state.username.length >= 3 &&
								this.state.password &&
								this.state.password.length >= 3)
							}
						>
							{t('Login.login')}
						</Button>
					</p>
					<p style={{ color: 'black' }}>{process.env.NODE_ENV}</p>
					<p style={{ color: 'black' }}>{process.env.REACT_APP_BASE_URL}</p>

					{/* <Button onClick={this.pushPage}>regist</Button> */}
				</section>
			</Page>
		);
	}
}

class LoginNav extends React.Component<Props, {}> {
	renderPage = (route: any, navigator: Navigator) => {
		route.props = route.props || {};
		route.props.navigator = navigator;

		return React.createElement(route.component, route.props);
	};

	render() {
		return (
			<Navigator
				animation='slide'
				initialRoute={{
					component: Login,
					props: {
						exhibitionName: this.props.exhibitionName,
						orgLogo: this.props.orgLogo,
					},
				}}
				renderPage={this.renderPage}
			/>
		);
	}
}

export default withTranslation()(Login);
