import React from 'react';

import { useTranslation, WithTranslation } from 'react-i18next';

import ons from 'onsenui';
import { Page, List, ListItem, Icon, Switch, SwitchChangeEvent } from 'react-onsenui';

import { commonStore, dashboardStore } from '../../stores';

import pjson from '../../../package.json';

type AppLanguage = 'zh-CN' | 'en';

const SideBar = (): JSX.Element => {
	const { t, i18n } = useTranslation();

	const toggleLanguage = () => {
		const lng = i18n.language as AppLanguage;
		i18n.changeLanguage(lng === 'en' ? 'zh-CN' : 'en');
	};

	const doLogout = (): void => {
		ons.notification.confirm({
			title: t('Common.Tip'),
			message: t('Dashboard.confirmLogout'),
			buttonLabels: [t('Common.Cancel'), t('Common.OK')],
			cancelable: true,
			callback: (index: number): void => {
				if (index === 1) {
					dashboardStore.setDrawerOpen(false);
					commonStore.removeToken();
				}
			},
		});
	};

	return (
		<Page>
			<List className='drawer'>
				<div className='drawer-header' style={{ margin: '30px auto' }}>
					<p className='text-center font-24'>ECO2S</p>
					<p className='text-center font-14'>verison: {pjson.version}</p>
				</div>

				<ListItem tappable onClick={toggleLanguage}>
					<div className='left'>
						<Icon icon='md-refresh' style={{ color: '#007aff' }} size={24} />
					</div>
					<div className='center font-16'>{t('Dashboard.toggleLanguage')}</div>
				</ListItem>

				<ListItem tappable onClick={doLogout}>
					<div className='left'>
						<Icon icon='md-delete' style={{ color: '#e91e63' }} size={24} />
					</div>
					<div className='center font-16'>{t('Dashboard.logout')}</div>
				</ListItem>
			</List>
		</Page>
	);
};

export default SideBar;
